}

.react-precision-slider {
  text-align: left;
  border-bottom: 1px solid #efefef;
}
.react-precision-slider div:first-child {
  display: grid;
  grid-template-columns: 20% 50% 30%;
}

.rpc-reset {
  cursor: pointer;
